import styled from "styled-components"

export const smallText = styled.p`
  font-family: "Inter", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 1.5;

  @media only screen and (max-width: 650px) {
    font-size: 14px;
  }
`

